// pages/login.tsx
import { useState } from "react";
import { useRouter } from "next/router";

const Login = () => {
  const [statusMessage, setStatusMessage] = useState<string>("");
  const router = useRouter();

  const handleLogin = async (event: React.FormEvent) => {
    event.preventDefault();

    const email = (event.target as HTMLFormElement).email.value;
    const password = (event.target as HTMLFormElement).password.value;

    try {
      const response = await fetch(
        `${process.env.NEXT_PUBLIC_API_URL}/api/users/login`,
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ email, password }),
        }
      );

      if (response.ok) {
        const data = await response.json();
        const token = data.token;

        // Menyimpan token ke sessionStorage
        sessionStorage.setItem("authToken", token);

        setStatusMessage("Login berhasil!");

        // Redirect ke halaman yang membutuhkan token
        router.push("/dashboard"); // Ganti dengan halaman yang sesuai
      } else {
        setStatusMessage("Email atau password salah.");
      }
    } catch (error) {
      setStatusMessage("Terjadi kesalahan saat login.");
    }
  };

  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-gray-100 p-6">
      <h1 className="text-4xl font-bold mb-6">Login</h1>

      <form onSubmit={handleLogin} className="w-full max-w-md space-y-4">
        <div>
          <label htmlFor="email" className="block text-lg">
            Email
          </label>
          <input
            type="email"
            id="email"
            name="email"
            className="w-full px-4 py-2 border rounded-md"
            required
          />
        </div>

        <div>
          <label htmlFor="password" className="block text-lg">
            Password
          </label>
          <input
            type="password"
            id="password"
            name="password"
            className="w-full px-4 py-2 border rounded-md"
            required
          />
        </div>

        <button
          type="submit"
          className="w-full bg-blue-600 text-white py-2 rounded-lg"
        >
          Login
        </button>
      </form>

      {statusMessage && (
        <div className="mt-4 text-center text-lg text-green-500">
          {statusMessage}
        </div>
      )}
    </div>
  );
};

export default Login;
